.container {
    max-width: 1440px;
    width: 100%;
    margin: 0 auto;
    padding: 10px 20px;
    display: grid;
    grid-template-columns: 35% 1fr;
    gap: 20px;

    @media screen and (max-width: 768px){
        grid-template-columns: 1fr;
        grid-template-rows: auto 1fr;
    }
}


